.demo {
  padding: 0!important;
  height: 100%;
}
.ant-layout-sider-trigger {
  display: none;
}
.example {
  padding-top: 0!important;
}
.header {
  padding-left: 10px;
}
.ant-card-body {
  overflow: auto;
}
.eboard-mobile {
  position: relative;
  height: 600px;
  width: 100%;
  display: block;
}
.eboard-pc {
  position: relative;
  height: 100%;
  width: 100%;
  display: inline-block;
}
.ant-layout-content {
  position: relative;
}
.ant-card {
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  height: 100%;
}
.ant-card-body {
  position: absolute;
  top: 57px;
  bottom: 0;
  width: 100%;
}
